<template>
  <HeaderEl 
    :name="sceneName"
  ></HeaderEl> 

  <!-- 본문 -->
  <main class="wrap" @onScroll="onScroll">
    <section class="abstract">
      <h2>버전관리
        <span>내가 시도한 것들</span>
      </h2>
      <figure>
        <img src="../assets/images/app_sourcetree.png" alt="sourcetree 앱 아이콘">
        <figcaption>sourcetree 앱 아이콘 이미지</figcaption>
      </figure>
      <div class="git_means"></div>
    </section>
  </main>
</template>

<script>
import HeaderEl from './common/Header.vue';

export default {
  name: 'SourceTree',
  components: {
    HeaderEl
  },
  data: () => ({
    sceneName: '소스트리',
    window: {
      width: 0,
      height: 0,
    }
  }),
  mounted() {
    this.onScroll();
    window.addEventListener("scroll", this.onScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll, true)
  },
  methods: {
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const wrap = document.querySelector('.wrap'),
            // playing = document.querySelector('.playing'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = wrap.scrollTop;
      this.window.height = window.innerHeight;
      wrap.height = this.window.height - 52;    // header.innerHeight JS표현식 나중에 알아보기
      wrap.height = wrap.height * 5;
      console.log(this.windowTop);
      console.log(wrap.height);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
        console.log(wrap.height);
      } else {
        header.classList.remove('scrolled');
      }
    }
  }
}
</script>

