import { createWebHistory, createRouter } from 'vue-router'
import HomeW from '../components/HomeW.vue'
import TestW from '../components/TestW.vue'
import NotFound from '../components/NotFound.vue'
import GuideW from '../components/GuideW.vue'
import DietDiary from '../components/DietDiary.vue'
import IsThis from '../components/IsThis.vue'
import MyTrip from '../components/MyTrip.vue'
import ClassObject from '../components/ClassObject.vue'
import UiCompo from '../components/UiComponent.vue'
import SourceTree from '../components/SourceTree.vue'
import IsCategory from '../components/IsCategory.vue'

const routes = [
  {
    path: '/isthis',
    component: IsThis,
  },
  {
    path: '/testw',
    component: TestW,
  },
  {
    path: '/classobject',
    component: ClassObject,
  },
  {
    path: '/',
    component: HomeW,
  },
  {
    path: '/guidew',
    component: GuideW,
  },
  {
    path: '/dietdiary',
    component: DietDiary,
  },
  {
    path: '/mytrip',
    component: MyTrip,
  },
  {
    path: '/uicompo',
    component: UiCompo,
  },
  {
    path: '/sourcetree',
    component: SourceTree,
  },
  {
    path: '/iscategory',
    component: IsCategory,
    children: [
      {
        path: '/testw',
        component: TestW,
      },
      { 
        path: '/mytrip',
        component: MyTrip,
      },
      {
        path: '/classobject',
        component: ClassObject,
      },
      {
        path: '/isthis',
        component: IsThis,
      },
    ] 
  },
  {
    path: '/notfound',
    component: NotFound,
  },
  {
    // path: '*',
    // component: NotFound,
    path: '/:pathMatch(.*)*',
    redirect: '/notfound',
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;