<script>
import HeaderEl from './common/Header.vue';

export default {
  // el: '#test',
  name: 'UiCompo',
  components: {
    HeaderEl,
  },
  data: () => ({
    sceneName: 'UI 컴포넌트',
    isIcofont: false,
    icofontCode: false,
    isColor: false,
    colorCode: false,
    b: ['웹폰트', '인풋', '컬러'],
  }),
  beforeUnmount() {
    window.removeEventListener('scroll', this.onScroll, true)
  },
  mounted() {
    // 타 페이지에도 target 이벤트 영향 생김.., 일반 클릭이벤트로 변경해야
    this.codeOpen();
    this.icofontRadius();
    this.onScroll();
    document.addEventListener('click', this.codeOpen);
    document.addEventListener('click', this.icofontRadius);

    // window.addEventListener 는 (이벤트: click) only 1개만 감지
    // window.addEventListener('click', this.icofontRadius);
    window.addEventListener('scroll', this.onScroll, true);
  },
  methods: {
    icofontRadius(event) {
      if(event) {
        // console.log(11);
        let target = event.target,
            classList = target.classList;
            // currentTarget 은 Array를 리턴하므로 사용시 주의!!
        console.log(classList.contains('borderBottomRightRadius'));
        if(classList.contains('borderBottomRightRadius') == false) {
          classList.add('borderBottomRightRadius');
        } else {
          classList.remove('borderBottomRightRadius');
        }
      }
    },
    codeOpen(event2) {
      if(event2) {
        // 방법: target;   
        // 태그: .dropdown @click="함수명(e)"
        // classList나 add는 클래스(Array) 꼭 확인, 이름: '.'제외
        let target = event2.target;
        console.log(target);
        console.log(target.classList.contains('isOpen'));
        if(target != null) {
          if(target.tagName == 'BUTTON') {
            if(target.classList.contains('isOpen') == false) { 
              target.classList.add('isOpen') 
            } else { target.classList.remove('isOpen') }
          }
        } 
      }
    },
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const selector = document.querySelector('.wrap'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = selector.scrollTop;
      console.log(this.windowTop, this.scrolledTop);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    }
  }
}
</script>

