<template>
  <HeaderEl
    class="header"
    :name="sceneName">
  </HeaderEl>

  <!-- 본문 -->
  <main class="wrap" @onScroll="onScroll">
    <h2>
      <label><small>1</small></label>
      폰트<span> 스타일</span>
    </h2>
    <VforRepeat
      :items="fontgroup01"></VforRepeat>
    <VforRepeat 
      :paragraphStyle="{ mainText: isParagraph }" 
      :items="fontgroup02"></VforRepeat>
  </main>
</template>

<script>
import HeaderEl from './common/Header.vue';
import VforRepeat from './VforRepeat.vue';

export default {
  name: 'GuideW',
  components: {
    HeaderEl,
    VforRepeat
  },
  data: () => ({
    sceneName: '스타일 가이드',
    isParagraph: true,
    fontgroup01: [
      {
        descKorean: '제목 크기',
        size: 'xl',
        unit: '33px',
        visual: '<p>제목 폰트 Size</p>'
      },
      {
        size: 'lg',
        unit: '30px',
        visual: '<p>제목 폰트 Size</p>'
      },
      {
        size: 'md',
        unit: '27px',
        visual: '<p>제목 폰트 Size</p>'
      }
    ],
    fontgroup02: [
      {
        descKorean: '단락 크기',
        size: 'lg',
        unit: '16px',
        visual: '<p>본문 폰트 Size</p>'
      },
      {
        size: 'md',
        unit: '14px',
        visual: '<p>본문 폰트 Size</p>'
      },
      {
        size: 'sm',
        unit: '12px',
        visual: '<p>본문 폰트 Size</p>'
      },
    ],
  }),
  mounted() {
    this.onScroll();
    window.addEventListener("scroll", this.onScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll, true)
  },
  methods: {
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const selector = document.querySelector('.wrap'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = selector.scrollTop;
      console.log(this.windowTop, this.scrolledTop);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    }
  }
}
</script>

