<template>
  <HeaderEl
    class="header"
    :class="{ twoDepth: status }"
    :name="sceneName"
    @click="goBack">
  </HeaderEl>

  <!-- 본문 -->
  <main class="wrap form_classobj" @onScroll="onScroll">
    <section class="definition">
      <div class="visual default">
        <img alt="gamefreak를 나타내는 이미지" src="">
        <img alt="sega" src="">
        <img alt="nintendo" src="">
      </div>
      <div class="series">
        <button v-for="brand in gameCompany" :key="brand">{{ brand }}</button>
      </div>
      <code class="code">
        class Softclay {<br>
        &nbsp;&nbsp;&nbsp;constructor(name, count, character) {<br>
          &nbsp;&nbsp;&nbsp;&nbsp;this.name = name;<br>
          &nbsp;&nbsp;&nbsp;&nbsp;this.count = count;<br>
          &nbsp;&nbsp;&nbsp;&nbsp;this.character = character;<br>
        &nbsp;&nbsp;&nbsp;}<br>
        }
      </code>
    </section>
    <div class="apply">
      <code class="code">
        const <span>gamefreak</span> = new Softclay('Pokemon', 1, null);<br>
        <span>gamefreak</span>.character = 'Lizard';
      </code>
      <div class="access">
        <h3>접근 방식</h3>
        <div>
          <p>class 객체를 어떤 식으로 이해하고 그림으로 표현했는가?</p>
          <img src="" alt="">
        </div>
      </div>
      <div class="extend">
        <h3>클래스 확장</h3>
        <div>
          <p>이미지 썸네일 사이즈(너비 x 높이) 구하기<br>
            문법: class A’ extends A<br>
            ( A: 원본 ,  A’: A확장 )</p>
          <img src="" alt="">
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import HeaderEl from './common/Header.vue';

export default {
  name: 'GuideW',
  components: {
    HeaderEl,
  },
  data: () => ({
    sceneName: 'Class 객체',
    status: true,
    gameCompany: ['1', '2', '3'],
  }),
  mounted() {
    this.hiddenBottomTab();
    this.onScroll();
    window.addEventListener("scroll", this.onScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll, true)
  },
  methods: {
    hiddenBottomTab(){
      this.bottomTab = document.querySelector('.bottom_tab');
      console.log(this.bottomTab);
      this.bottomTab.style.display = 'none';
    },  
    goBack () {
      this.bottomTab = document.querySelector('.bottom_tab');
      console.log(this.bottomTab);
      this.bottomTab.style.display = 'flex';
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    },
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const selector = document.querySelector('.wrap'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = selector.scrollTop;
      console.log(this.windowTop, this.scrolledTop);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    },
  }
}
</script>

