<template>
  <HeaderEl 
    class="header"
    :class="{ twoDepth: status }"
    :name="sceneName"
    @click="goBack">
  </HeaderEl>

  <!-- <div class='chkBox'>
    <label>
      <input type="checkbox" v-model="isChecked" true-value="Y" false-value="N">
    </label>
    <br>
    <span>체크값: {{ isChecked }}</span>
  </div> -->

  <div class="wrap">
    <div class="form_chkSet">
      <div class="column">
        <input type="checkbox" class="chkAll" value="all" v-model="allSelected">
        <label for="all">전체 동의</label>
      </div>  

      <div v-for="(item, index) in group" 
        class="column"
        :key="index">
        <input
          type="checkbox"
          class="chkInp"
          :id="item"
          :value="item"
          v-model="chkInput"
          :key="index">
        <label :for="item" :key="index + '1'"> {{ item }}</label>
      </div>
      <span class="result">check: {{ chkInput }}</span>
    </div>

    <!-- 필수, 선택 동의 추가 -->
  </div>
</template>

<script>
import HeaderEl from './common/Header.vue'

export default {
  name: 'TestW',
  components: {
    HeaderEl,
  },
  data: () => ({
    sceneName: '테스트',
    status: true,
    group: ["항목 1", "항목 2", "항목 3", "항목 4", "항목 5"],
    chkInput: [],
    // isChecked: 'N'
  }),
  mounted() {
    this.hiddenBottomTab();
  },
  methods: {
    hiddenBottomTab(){
      this.bottomTab = document.querySelector('.bottom_tab');
      console.log(this.bottomTab);
      this.bottomTab.style.display = 'none';
    },  
    goBack () {
      this.bottomTab = document.querySelector('.bottom_tab');
      console.log(this.bottomTab);
      this.bottomTab.style.display = 'flex';
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    },
  },
  computed: {
    allSelected: {
      //getter
      get: function() {
        return this.group.length === this.chkInput.length;
      },
      //setter
      set: function(e) {
        this.chkInput = e ? this.group : [];
      },
    },
  },
}
</script>

